.heading {
  margin-bottom: 4rem;

  &__subtitle {
    font-size: 1.9rem;
    font-weight: 100;
    letter-spacing: 1px;
    text-align: center;

    @media only screen and (min-width: 25em) {
      font-size: 2rem;
    }

    @media only screen and (min-width: 31.25rem) {
      font-size: 3rem;
    }
  }
}
